<template>
    <div>
        <div class="page-titles">
            <div class="row">
                <div class="col-12 col-sm-6">
                    <h3 class="text-themecolor">{{trans('transport.edit_vehicle_document')}}</h3>
                </div>
                <div class="col-12 col-sm-6">
                    <div class="action-buttons pull-right">
                        <button class="btn btn-info btn-sm" @click="$router.push('/transport/vehicle/document')"><i class="fas fa-list"></i> <span class="d-none d-sm-inline">{{trans('transport.vehicle_document')}}</span></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="container-fluid">
            <div class="card card-form">
                <div class="card-body p-t-20">
                    <vehicle-document-form :id="id"></vehicle-document-form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import vehicleDocumentForm from './form';

    export default {
        components : { vehicleDocumentForm },
        data() {
            return {
                id:this.$route.params.id
            }
        },
        mounted(){
            if(!helper.hasPermission('edit-vehicle-document')){
                helper.notAccessibleMsg();
                this.$router.push('/dashboard');
            }
        }
    }
</script>
